import { Pangkat } from 'src/app/sdk/hr/models/pangkat';
import { CompanyPangkatService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyPangkatService: CompanyPangkatService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Pangkat = {};
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id');
        this.form();
        this.getData()

    }

    id: string = "";
    getData() {
        this.companyPangkatService.getCompanyPangkatsId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }


    dataForm: FormGroupTypeSafe<Pangkat>;
    form() {
        this.dataForm = this.fb.group<Pangkat>(
            {
                id_pangkat: new FormControl('', [Validators.required]),
                position_level: new FormControl(''),
                kode_pangkat: new FormControl(''),
                level_pangkat: new FormControl(''),
                jumlah_pegawai: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyPangkatService.putCompanyPangkatsId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/pangkat/list'])
    }

}
