import { CompanyPangkatService, DirektoriVPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Pangkat } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private companyPangkatService: CompanyPangkatService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.searchData()
    }

    pageIndex = 1
    pageSize = 8
    total = 1
    listOfData = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''


    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.companyPangkatService
            .getCompanyPangkats(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey, sortValue: this.sortValue, search: this.search
            }))
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data.data)

                this.listOfData.forEach((pangkat, index)=>{
                this.getTotalPegawai(index, pangkat.id_pangkat);
                })
            })
    }

    getTotalPegawai(index, id_pangkat){
        let param = {
            id_pangkat : id_pangkat,
            total: true
        }
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisis(JSON.stringify(param)).subscribe(
            data=> {
                this.listOfData[index]['total_pegawai'] = data.data[0]['total']
            }
        )
    }
    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }


    delete(id)
    {
        this.spinner.show();
        this.companyPangkatService.deleteCompanyPangkatsId(id).subscribe(
            data => {
                this.searchData();
                this.spinner.hide();
            }
        )
    }

    batalDelete()
    {
        this.nzMessageService.info('click cancel');
    }


}
