import { Pangkat } from 'src/app/sdk/hr/models/pangkat';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyPangkatService } from 'src/app/sdk/hr/services';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyPangkatService: CompanyPangkatService,

        private spinner: NgxSpinnerService,
        private router: Router
    ) { }

    data: Pangkat = {};
    ngOnInit() {
        this.form();

    }

    dataForm: FormGroupTypeSafe<Pangkat>;
    form() {
        this.dataForm = this.fb.group<Pangkat>(
            {
                position_level: new FormControl('', [Validators.required]),
                kode_pangkat: new FormControl(''),
                level_pangkat: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyPangkatService.postCompanyPangkats(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/pangkat/list'])
    }

}
