import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { CompanyPangkatService, DirektoriVPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private pesan: PesanService,
        private fb: FormBuilderTypeSafe,
        private companyPangkatService: CompanyPangkatService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    toogleUpload = false

    dataGrade: any = {}


    id: string = ''

    ngOnInit() {
        this.id = this.routerActive.snapshot.paramMap.get('id')
        console.log(this.id)
        this.getDetail();
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }


    getDetail() {
        this.spinner.show()
        this.companyPangkatService.getCompanyPangkatsId(this.id).subscribe(
            response => {
                this.dataGrade = response.data;
                this.getTotalPegawai(response.data.id_pangkat)
                    this.spinner.hide()



            }
        )

    }

    getTotalPegawai(id_pangkat) {
        let param = {
            id_jabatan: id_pangkat,
            total: true
        }
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisis(JSON.stringify(param)).subscribe(
            data => {
                this.dataGrade['total_pegawai'] = data.data[0]['total']
            }
        )
    }


    goToBack() {

        this.router.navigate(['/hr/company/module/pangkat/list'])
    }


}
